interface IResult {
  result: (string | number)[][];
}

function MatchScoreItem({ result }: IResult) {
  return (
    <>
      {result.map(([score, date, ...num]) => (
        <p>
          {score}! : {num.map((item) => item + ' ')}
          : {date}
        </p>
      ))}
    </>
  );
}
export default MatchScoreItem;
